<template>
  <div>
    <div class="container1 fl-ex fl-co ju-be">
      <div class="pie" ref="chart"></div>
      <div class="sign">
        <div class="sign1 fl-ex al-it ju-be">
          <div style="background-color:#ffa339;"></div>
          <p>
            注册总数:
            <span>3000</span>人
          </p>
          <div style="background-color:#ffa4a7;"></div>
          <p>
            均参人数:
            <span>150</span>人
          </p>
        </div>
        <div class="sign1 fl-ex al-it ju-be">
          <div style="background-color:#7bb5ff;"></div>
          <p>
            荣誉奖励:
            <span>300</span>人
          </p>
          <div style="background-color:#67c759;"></div>
          <p>
            礼品奖励:
            <span>150</span>人
          </p>
        </div>
        <div class="sign1 fl-ex al-it">
          <div style="background-color:#20f9e8;"></div>
          <p style="margin-left:1rem;">
            现金奖励:
            <span>150</span>人
          </p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
const echarts = require("echarts");
export default {
  data() {
    return {};
  },
  methods: {
    initCharts() {
      let myChart = echarts.init(this.$refs.chart);
      myChart.setOption({
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b} : {c} ({d}%)"
        },
        calculable: true,
        series: [
          {
            color: [
              "#ffa336", //黄色
              "#2ef2e8", //青色
              "#67c259", //绿色
              "#7eb9ff", //蓝色
              "#fe9ea0" //粉色
            ],
            type: "pie",
            radius: [35, 125],
            center: ["50%", "60%"],
            roseType: "radius",
            label: {
              normal: {
                show: false
              },
              emphasis: {
                show: true
              }
            },
            lableLine: {
              normal: {
                show: false
              },
              emphasis: {
                show: true
              }
            },
            data: [
              { value: 20, name: "注册总数3000人" },
              { value: 11, name: "现金奖励150人" },
              { value: 11, name: "礼品奖励150人" },
              { value: 17, name: "荣誉奖励300人" },
              { value: 11, name: "均参人数150人" }
            ]
          }
        ]
      });
    }
  },
  mounted() {
    this.initCharts();
  }
};
</script>

<style scoped>
.container1 {
  width: 100%;
  height: 25.5rem;
}
.container1 .pie {
  width: 95%;
  height: 19rem;
  margin: 0 auto;
}
.container1 .sign {
  width: 77%;
  height: 5.4rem;
  margin: 0 auto;
}
.container1 .sign .sign1 {
  width: 100%;
  height: 1.8rem;
}
.container1 .sign .sign1 > div {
  width: 1.2rem;
  height: 0.5rem;
}
.container1 .sign .sign1 > p {
  font-size: 0.85rem;
  color: black;
  text-align: left;
}
</style>


